
.title-box {
  width: 100%;
  height: 5rem;
  text-align: center;
  line-height: 5rem;
  font-size: 0.9rem;
}
.btn-box {
  width: 100%;
}
.btn-box .btn {
  display: block;
  width: 80%;
  height: 1.2rem;
  margin: 0 auto;
  border-radius: 1rem;
  font-size: 0.45rem;
  background: #478e90;
}
.btn-box .btn.touching {
  background: #268184;
}


