
.game-container {
  width: 100%;
  height: 100%;
}
.game-container .text-box {
  position: absolute;
  top: 0.1rem;
  width: 100%;
  height: 1rem;
  line-height: 1rem;
  text-align: center;
  font-size: 0.8rem;
  color: #f00;
}
.game-container .table {
  width: 100%;
/*transform translate3d(0, -25vh, 0)*/
  transition: transform ease 1s;
}
.game-container .box {
  display: table-cell;
  width: 34vw;
  height: 25vh;
  box-sizing: border-box;
  border: 1px solid #222;
  background: #fff;
}
.game-container .black-box {
  width: 100%;
  height: 100%;
  background: #000;
}
.game-container .white-box {
  width: 100%;
  height: 100%;
  background: #fff;
}
.game-container .mask {
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.7);
  position: absolute;
  top: 0;
  left: 0;
}
.game-container .modal {
  width: 7rem;
  height: 5rem;
  background: #fff;
  border-radius: 0.5rem;
  border: 0.05rem solid #000;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
/*transform translate3d(-50%, -50%, 0)*/
  overflow: hidden;
}
.game-container .modal .msg {
  width: 100%;
  text-align: center;
  font-size: 0.6rem;
  padding: 0.5rem 1rem;
  box-sizing: border-box;
}
.game-container .modal .title {
  font-size: 0.5rem;
  color: #999;
}
.game-container .modal .mark-number {
  color: #f00;
}
.game-container .modal .btn-group {
  width: 100%;
  height: 1.4rem;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  bottom: 0;
}
.game-container .modal .btn-group .btn {
  background: none;
  color: #000;
  display: inline-block;
  width: 50%;
  height: 100%;
}
.modal-enter-active,
.modal-leave-active {
  transition: all 0.3s ease;
  transform: scale(1);
}
.modal-enter,
.modal-leave-active {
  transform: scale(1.1);
  opacity: 0;
}


