}
.view {
  transition: opacity 0.3s ease, transform 0.3s ease;
}
.view-enter,
.view-leave {
  opacity: 0;
  transform: translate3d(0.133333333333333rem, 0, 0);
}
.staggered-transition {
  transition: opacity 0.5s ease, height 0.5s ease, transform 0.5s ease;
  margin: 0;
}
.staggered-enter,
.staggered-leave {
  opacity: 0;
  height: 0;
  transform: translate3d(0, 0.133333333333333rem, 0);
}
.rightToLeft-transition {
  transition: opacity 0.3s ease, height 0.3s ease, transform 0.3s ease;
  opacity: 1;
  transform: translate3d(0, 0, 0);
}
.rightToLeft-enter,
.rightToLeft-leave {
  opacity: 0;
  transform: translate3d(0.3rem, 0, 0);
}
.formBottom-transition {
  transition: opacity 0.3s ease, transform cubic-bezier(0.175, 0.885, 0.47, 1.5) 0.3s;
  opacity: 1;
  transform: translate3d(0, 0, 0);
}
.formBottom-enter,
.formBottom-leave {
  opacity: 0;
  transform: translate3d(0, 100%, 0);
}
.formTop-transition {
  transition: opacity 0.3s ease, height 0.3s ease, transform 0.3s ease;
  opacity: 1;
  transform: translate3d(0, 0, 0);
}
.formTop-enter,
.formTop-leave {
  opacity: 0;
  transform: translate3d(0, -100%, 0);
}
.bottom-to-top-transition {
  transition: opacity 0.3s ease, height 0.3s ease, transform 0.3s ease;
  opacity: 1;
  transform: translate3d(0, 0, 0);
}
.bottom-to-top-enter,
.bottom-to-top-leave {
  opacity: 0;
  transform: translate3d(0, 1%, 0);
}
.scale-transition {
  transition: opacity 0.3s ease, transform 0.3s ease;
  opacity: 1;
  transform: scale(1);
}
.scale-enter,
.scale-leave {
  opacity: 0;
  transform: scale(1.15);
}
.modal-transition {
  transition: opacity 0.2s ease, transform 0.2s ease;
  opacity: 1;
  transform: scale(1);
}
.modal-enter,
.modal-leave {
  opacity: 0;
  transform: scale(1.25);
}
.opacity-transition {
  transition: opacity 0.5s ease;
  opacity: 1;
}
.opacity-enter,
.opacity-leave {
  opacity: 0;
}
